import React, {useState} from 'react';
import IconPicker from "../IconPicker";

const PostTypeIcon = ({id, label, setValue, validate, register, errors, isRequired, defaultValue, description, wizard}) => {

    const error = errors[id];

    /**
     * set icon value
     * @param value
     */
    const callbackIcon = (value) => {
        setValue(id, value);
    };

    return (
        <div className="acpt-form-group">
            <div className="acpt-form-label-wrapper">
                <label htmlFor={id}>
                    {label}
                    {isRequired && (
                        <span className="required">*</span>
                    )}
                </label>
                {wizard && (
                    <span className="wizard" dangerouslySetInnerHTML={{ __html: wizard }} />
                )}
            </div>
            <div className="acpt-form-control-wrapper">
                <IconPicker
                    error={error}
                    id={id}
                    defaultValue={defaultValue}
                    register={register}
                    callback={callbackIcon}
                    validate={validate}
                />
                {description && (
                    <span className="description">{description}</span>
                )}
                {error && <div className="invalid-feedback">{error.message}</div>}
            </div>
        </div>
    );
};

export default PostTypeIcon;