import React from 'react';
import {useForm} from "react-hook-form";
import InputText from "../../reusable/Form/InputText";
import {useDispatch, useSelector} from "react-redux";
import {stepForward} from "../../../redux/actions/stepsActions";
import StepsButtons from "../../reusable/Steps/StepsButtons";
import Checkboxes from "../../reusable/Form/Checkboxes";
import ReactSelect from "../../reusable/Form/ReactSelect";
import {dashiconList} from "../../../constants/dashicons";
import {filterByValue} from "../../../utils/objects";
import {sluggifyString} from "../../../utils/strings";
import {asyncIsPostTypeNameValid, isPostTypeNameValid} from "../../../utils/validation";
import Layout from "../../reusable/Layout";
import ActionsBar from "../../reusable/Layout/ActionsBar";
import StepsHeader from "../../reusable/Steps/StepsHeader";
import Breadcrumbs from "../../reusable/Layout/Breadcrumbs";
import useTranslation from "../../../hooks/useTranslation";
import PostTypeIcon from "../../reusable/Form/PostTypeIcon";

const BasicStep = ({postType, headings, edit}) => {

    // manage global state
    const {fetched} = useSelector(state => state.fetchPostTypesReducer);
    const dispatch = useDispatch();

    // handle form
    let supports = [];
    if(fetched.length > 0){
        supports = fetched[0].supports;
    }

    const { control, register, handleSubmit, setValue, formState: {errors, isValid} } = useForm({
        mode: 'all',
        defaultValues: {
            post_name: fetched.length > 0 ? fetched[0].name : null,
            singular_label: fetched.length > 0 ? fetched[0].singular : null,
            plural_label: fetched.length > 0 ? fetched[0].plural : null,
            icon: fetched.length > 0 ? fetched[0].icon : null,
        }
    });

    const handlePostNameChange = (post_name) => {
        setValue('post_name', sluggifyString(post_name, 20));
    };

    const onSubmit = (data) => {
        dispatch(stepForward(data));
    };

    return(
        <Layout>
            <form onSubmit={handleSubmit(onSubmit)}>
                <ActionsBar
                    title={postType ? useTranslation("Edit Custom Post Type") : useTranslation("Create new Custom Post Type")}
                    actions={<StepsButtons isValid={isValid} next={2} />}
                />
                <main>
                    <Breadcrumbs crumbs={[
                        {
                            label: useTranslation("Registered Custom Post Types"),
                            link: "/"
                        },
                        {
                            label: postType ? useTranslation("Edit Custom Post Type") : useTranslation("Create new Custom Post Type")
                        }
                    ]} />
                    <div className="acpt-card">
                        <StepsHeader headings={headings}/>
                        <div className="acpt-card__inner">
                            <InputText
                                id="post_name"
                                label={useTranslation("Post name")}
                                placeholder={useTranslation("Post name")}
                                readOnly={fetched.length > 0}
                                defaultValue={fetched.length > 0 ? fetched[0].name : null }
                                description={useTranslation("The post name/slug. Used for various queries.")}
                                register={register}
                                errors={errors}
                                isRequired={true}
                                onChangeCapture={ e => handlePostNameChange(e.currentTarget.value) }
                                validate={{
                                    validate: edit ? isPostTypeNameValid : asyncIsPostTypeNameValid,
                                    required: useTranslation("This field is mandatory")
                                }} />
                            <InputText
                                id="singular_label"
                                label={useTranslation("Singular label")}
                                placeholder={useTranslation("(e.g. Movie)")}
                                defaultValue={fetched.length > 0 ? fetched[0].singular : null }
                                description={useTranslation("Used when a singular label is needed")}
                                register={register}
                                errors={errors}
                                isRequired={true}
                                validate={{
                                    required: useTranslation("This field is mandatory")
                                }} />
                            <InputText
                                id="plural_label"
                                label={useTranslation("Plural label")}
                                placeholder={useTranslation("(e.g. Movies)")}
                                defaultValue={fetched.length > 0 ? fetched[0].plural : null }
                                description={useTranslation("Used for the post type admin menu item")}
                                register={register}
                                errors={errors}
                                isRequired={true}
                                validate={{
                                    required: useTranslation("This field is mandatory")
                                }} />
                            <PostTypeIcon
                                id="icon"
                                label={useTranslation("Icon")}
                                placeholder={useTranslation("Associated icon")}
                                description={useTranslation("Displayed on the sidebar of the admin panel")}
                                register={register}
                                errors={errors}
                                defaultValue={fetched.length > 0 ? fetched[0].icon : null }
                                setValue={setValue}
                                isRequired={true}
                                validate={{
                                    required: useTranslation("This field is mandatory")
                                }}
                            />
                            <Checkboxes
                                id="support"
                                label={useTranslation("Support")}
                                wizard={`${useTranslation("Add support for various available post edit features. For more info")} <a target='_blank' href='https://developer.wordpress.org/reference/functions/register_post_type/#supports'>${useTranslation("see here")}<a/>.`}
                                values={{
                                    "title": {
                                        "value": "title",
                                        "checked": fetched.length > 0 ? supports.includes('title') : true,
                                    },
                                    "editor": {
                                        "value": "editor",
                                        "checked": fetched.length > 0 ? supports.includes('editor') : true,
                                    },
                                    "thumbnail": {
                                        "value": "thumbnail",
                                        "checked": fetched.length > 0 ? supports.includes('thumbnail') : true,
                                    },
                                    "excerpt": {
                                        "value": "excerpt",
                                        "checked": fetched.length > 0 ? supports.includes('excerpt') : true,
                                    },
                                    "author": {
                                        "value": "author",
                                        "checked": fetched.length > 0 ? supports.includes('author') : false,
                                    },
                                    "trackbacks": {
                                        "value": "trackbacks",
                                        "checked": fetched.length > 0 ? supports.includes('trackbacks') : false,
                                    },
                                    "custom-fields": {
                                        "value": "custom-fields",
                                        "checked": fetched.length > 0 ? supports.includes('custom-fields') : false,
                                    },
                                    "comments": {
                                        "value": "comments",
                                        "checked": fetched.length > 0 ? supports.includes('comments') : false,
                                    },
                                    "revisions": {
                                        "value": "revisions",
                                        "checked": fetched.length > 0 ? supports.includes('revisions') : false,
                                    },
                                    "page-attributes": {
                                        "value": "page-attributes",
                                        "checked": fetched.length > 0 ? supports.includes('page-attributes') : false,
                                    },
                                    "post-formats": {
                                        "value": "post-formats",
                                        "checked": fetched.length > 0 ? supports.includes('post-formats') : false,
                                    }
                                }}
                                register={register}
                                errors={errors}
                            />
                        </div>
                    </div>
                </main>
            </form>
        </Layout>
    )
};

export default BasicStep;